@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@300;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 4.4rem;
  width: 100%;
  font-family: "Spartan", sans-serif;
  font-weight: 400;
  position: fixed;
  z-index: 10;
  background-color: #0C0E1E;
  opacity: 0;
  animation: nav-slidein 1.3s ease-out 0s 1 normal forwards;
}

@keyframes nav-slidein {
  0% {
    opacity: 0;
    transform: translateY(-10rem);
  }
  65% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (min-width: 768px) {
  nav {
    animation-delay: 0.3s;
  }
}

#nav-icon {
  height: 2.8rem;
  width: 2.8rem;
  margin-left: 1.4rem;
  background-image: url(/static/media/logo_light.66a701c6.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transform: translateY(-40rem);
  animation: nav-icon-slidein 1s ease-out 1.5s 1 normal forwards;
}

@keyframes nav-icon-slidein {
  0% {
    opacity: 0;
    transform: translateY(-40rem);
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (min-width: 768px) {
  #nav-icon {
    animation-delay: 2.23s;
  }
}

#nav-links {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin: 0;
  display: none;
}

@media (min-width: 768px) {
  #nav-links {
    display: flex;
  }
}

#nav-links .nav-li-group {
  list-style: none;
  transform: translateY(-40rem);
}

#nav-links a {
  text-decoration: none;
  margin: 0 0.7rem;
  padding: 1.1rem 1.4rem;
  color: #F1F1F1;
  transition: padding 0.4s ease-out;
}

#nav-links a:hover {
  padding: 1.1rem 2.3rem;
}

#nav-links #nav-home {
  animation: nav-home-slidein-first 1s ease-out 2.3s 1 normal forwards;
}

@keyframes nav-home-slidein-first {
  0% {
    opacity: 0;
    transform: translateY(-40rem);
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

#nav-links #nav-projects {
  animation: nav-projects-slidein-first 1s ease-out 2.37s 1 normal forwards;
}

@keyframes nav-projects-slidein-first {
  0% {
    opacity: 0;
    transform: translateY(-40rem);
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

#nav-links #nav-others {
  animation: nav-others-slidein-first 1s ease-out 2.44s 1 normal forwards;
}

@keyframes nav-others-slidein-first {
  0% {
    opacity: 0;
    transform: translateY(-40rem);
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

#nav-links-mobile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 4.2rem;
  left: 0;
  width: 100%;
  opacity: 0.95;
  list-style: none;
  background-color: #0C0E1E;
}

#nav-links-mobile li {
  padding: 2rem 0;
}

#nav-links-mobile a {
  text-decoration: none;
  margin: 2rem 0.7rem;
  padding: 1.1rem 1.4rem;
  color: #F1F1F1;
}

@media (min-width: 768px) {
  #nav-links-mobile {
    display: none;
  }
}

#nav-hamburger {
  margin-right: 1.1rem;
  padding: 0 0.5rem;
  font-size: 3rem;
  color: #F1F1F1;
  transform: translateY(-40rem);
  animation: nav-hamburger-slidein 1s ease-out 1.57s 1 normal forwards;
}

#nav-hamburger:hover {
  cursor: pointer;
}

@keyframes nav-hamburger-slidein {
  0% {
    opacity: 0;
    transform: translateY(-40rem);
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (min-width: 768px) {
  #nav-hamburger {
    display: none;
  }
}

#home {
