  background-size: contain;
  background-repeat: no-repeat;
  transform: translate(-12rem, 1rem) scale(1.9);
  z-index: -1;
  opacity: 0;
  animation: graphic-slidein-mobile 1.3s ease-out 0s 1 normal forwards;
}

@keyframes graphic-slidein-mobile {
  0% {
    opacity: 0;
    transform: translate(-12rem, 1rem) scale(1.9);
  }
  65% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translate(-3rem, 1rem) scale(1.9);
  }
}

@media (min-width: 768px) {
  #home #home-visual-graphic {
    background-size: contain;
    transform: rotateZ(45deg) translate(-16rem, 3rem) scale(2.4);
    opacity: 0;
    animation: graphic-slidein-tablet 1.3s ease-out 0.3s 1 normal forwards;
  }
  @keyframes graphic-slidein-tablet {
    0% {
      opacity: 0;
      transform: rotateZ(45deg) translate(-24rem, 3rem) scale(2.4);
    }
    65% {
