    animation: graphic-slidein-laptop 1.3s ease-out 0.3s 1 normal forwards;
  }
  @keyframes graphic-slidein-laptop {
    0% {
      opacity: 0;
      transform: rotateZ(45deg) translate(-24rem, 3rem) scale(2.7);
    }
    65% {
      opacity: 0;
    }
    100% {
      opacity: 1;
      transform: rotateZ(45deg) translate(-16rem, 3rem) scale(2.7);
    }
  }
}

#home #home-info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 70vh;
  width: 100%;
}

@media (min-width: 768px) {
