}

@keyframes greeting-slidein {
  0% {
    transform: translateY(-20rem);
    opacity: 0;
  }
  65% {
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@media (min-width: 768px) {
  #home #home-info #home-greeting {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #home #home-info #home-greeting #greeting-msg {
