  padding: 0 1rem;
}

#home #home-info #home-description .home-desc-group {
  text-align: center;
  font-weight: 300;
  padding: 0.3rem 0.5rem;
  opacity: 0;
}

#home #home-info #home-description #home-h1-derian {
  animation: derian-slidein-first 1s ease-out 1.6s 1 normal forwards;
}

@keyframes derian-slidein-first {
  0% {
    transform: translateX(-40rem);
    opacity: 0;
  }
  55% {
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@media (min-width: 768px) {
  #home #home-info #home-description #home-h1-derian {
    animation-duration: 1s;
    animation-delay: 2.3s;
  }
}

#home #home-info #home-description #home-h1-melvin {
  animation: melvin-slidein-second 1s ease-out 1.67s 1 normal forwards;
}

@keyframes melvin-slidein-second {
  0% {
    transform: translateX(-40rem);
    opacity: 0;
  }
  55% {
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@media (min-width: 768px) {
  #home #home-info #home-description #home-h1-melvin {
    animation-duration: 1s;
    animation-delay: 2.37s;
  }
}

#home #home-info #home-description #home-p-description {
  animation: desc-slidein-third 1s ease-out 1.74s 1 normal forwards;
}

@keyframes desc-slidein-third {
  0% {
    transform: translateX(-40rem);
    opacity: 0;
  }
  55% {
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@media (min-width: 768px) {
  #home #home-info #home-description #home-p-description {
    animation-duration: 1s;
    animation-delay: 2.44s;
  }
}

@media (min-width: 768px) {
  #home #home-info #home-description {
    height: 60%;
    width: 100%;
    align-items: flex-end;
  }
  #home #home-info #home-description .home-desc-group {
